
.header {
  background-color: #42b983;
}
.header-wrapper {
  width: 100%;
  height: auto;
  position: absolute;
  z-index: 2;
  top: 2%
}
.header-ul {
  text-decoration: none;
  list-style: none;
  text-align: center;
}
.header-ul li {
  display: inline-block;
  padding: 10px;
  width: 50px;
  color: #fff;
}
a{
  text-decoration: none;
  color: #fff;
}
.header-img {
  width: 100%;
  /*position: absolute;*/
  /*top: 0;*/
  height: 210px;
  background-color: #000;
}
.header-text {
  position: absolute;
  width: 100%;
  text-align: center;
  color: #fff;
  top: 15%;
  font-size: 25px;
}



